import React, {useState} from 'react'
import Layout from '../../components/Layout/Layout'
import toast from 'react-hot-toast';
import axios from 'axios'
import { useNavigate, useLocation } from 'react-router-dom';
import '../styles/AuthStyles.css'
import { useAuth } from '../../context/auth';



export default function Login() { 
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [auth, setAuth] = useAuth()
    const navigate = useNavigate()
    const location = useLocation()


    const handlSubmit = async (e) => {
        e.preventDefault();
        

        try {
            const res = await axios.post(`/api/v1/auth/login`, {
                email, password
            });  
            if(res && res.data.succes) {
                toast.success(res.data && res.data.message);
                setAuth({
                    ...auth,
                    user: res.data.user,
                    token: res.data.token
                })
                localStorage.setItem('auth', JSON.stringify(res.data))
                navigate(location.state || '/' );      
            }
            else{
                toast.error(res.data.message)
            }
        } catch (error) {
            toast.error("Something went wrong")
        }        
    }



  return (
    <Layout title={'Seŋo - Ful_Kel'}>
    <div className='form-container'>
         
         <form onSubmit={handlSubmit}>
         <h4 className="title">SEŊORDE</h4>
            <div className="mb-3">
                <input 
                    type="email" 
                    value={email}
                    onChange={e => setEmail(e.target.value)}
                    className="form-control" 
                    id="exampleInputEmail1" 
                    placeholder='Winndu Emeel ma'
                    required
                 />  
            </div>
            <div className="mb-3">
                <input 
                    type="password" 
                    value={password}
                    onChange={e => setPassword(e.target.value)}
                    className="form-control" 
                    id="exampleInputEmail1" 
                    placeholder='Naatnu finnde ma'
                    required
                 />  
            </div>
            <div className='mb-3'>
            <button type="button" className="btn btn-primary" onClick={() => {navigate('/forget-password')}}>
               Mi yejjitii finnde am
            </button>
            </div>
            
            <button type="submit" className="btn btn-primary">
               Seŋo
            </button>
            
        </form>
    </div>
</Layout>
  )
}
